import React from "react";
import { Dropdown } from "primereact/dropdown";

import "primereact/resources/themes/nova-light/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";

class TopicContent extends React.Component {
  state = {
    checked: false
  };

  handleCheck = () => {
    this.setState({
      checked: !this.state.checked
    });
  };

  citySelectItems = [
    { label: "New York", value: "NY" },
    { label: "Rome", value: "RM" },
    { label: "London", value: "LDN" },
    { label: "Istanbul", value: "IST" },
    { label: "Paris", value: "PRS" }
  ];

  render() {
    const { checked } = this.state;
    const {
      onChange,
      title,
      description,
      amount,
      submitted,
      typed
    } = this.props;

    console.log(submitted, checked);
    return (
      <div className="topic-content">
        <h2 className="title">Topic Content</h2>
        <p className="paragraph">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque sint
          aspernatur, voluptate adipisci ab qui laborum a veniam itaque error
          tempore sit impedit ullam veritatis? Atque nemo sequi cumque
          reiciendis.
        </p>
        <div className="tab-label">
          <label>
            <span>Title</span>
            <input
              type="text"
              placeholder="title"
              name="title"
              value={title}
              className={submitted && !title ? "G-invalid-input" : ""}
              onChange={onChange}
            />
          </label>
        </div>
        <div className="tab-label">
          <label>
            <div className="description-label">
              <span>Description</span>
              <span>min 500 symbols</span>
            </div>

            <textarea
              placeholder="Topic description"
              className={
                submitted && !description && !description.length < 500
                  ? "G-invalid-input"
                  : ""
              }
              name="description"
              value={description}
              onChange={onChange}
            ></textarea>
          </label>
        </div>
        <div className="tab-label">
          <label>
            <span>Rough project budget</span>
            <input
              type="number"
              placeholder="Write amount"
              name="amount"
              value={checked ? "" : amount}
              disabled={checked ? true : false}
              className={
                submitted && !amount
                  ? "G-invalid-input"
                  : submitted && checked
                  ? "helloworld"
                  : checked
                  ? "opacity"
                  : ""
              }
              onChange={onChange}
            />
            <div className="currency-select">
              <Dropdown
                value={this.state.city}
                options={this.citySelectItems}
                onChange={e => {
                  this.setState({ city: e.value });
                }}
                placeholder="Select a City"
              />
            </div>
          </label>
          <div className="check">
            <input
              type="checkbox"
              onChange={this.handleCheck}
              checked={checked}
              disabled={typed ? true : false}
            />
            <span>I am not sure how much amount is required</span>
          </div>
        </div>

        <button className="G-button" onClick={this.props.moveNextPage}>
          Continue
        </button>
      </div>
    );
  }
}

export default TopicContent;
